.navigation__btn-cont {
  position: absolute;
  width: 38px;
  height: 3px;
  background-color: #ffffff;
  border-radius: 3px; }
  .navigation__btn-cont::after, .navigation__btn-cont::before {
    content: "";
    position: absolute;
    top: 10px;
    left: 0;
    width: 38px;
    height: 3px;
    background-color: #ffffff;
    border-radius: 3px;
    transition: 0.3s ease; }
  .navigation__btn-cont::before {
    top: -10px; }

.navigation__btn {
  position: relative;
  z-index: 2;
  width: 38px;
  height: 38px;
  padding: 0;
  background: transparent;
  border: none;
  outline: none; }
  .navigation__btn--active .navigation__btn-cont {
    background-color: transparent; }
  .navigation__btn--active .navigation__btn-cont::after {
    transform: rotate(45deg) translate(-4px, -4px); }
  .navigation__btn--active .navigation__btn-cont::before {
    transform: rotate(-45deg) translate(-10px, 10px); }

.navigation__list {
  margin: 0;
  padding: 0;
  list-style: none;
  padding: 5px 0 0 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  visibility: hidden;
  overflow: hidden;
  backface-visibility: hidden;
  transform: translate3d(0, 0, 0);
  transform-style: preserve-3d;
  background-image: url("../img/back-header.png");
  background-repeat: no-repeat; }
  .navigation__list--active {
    visibility: visible;
    z-index: 1; }
  .navigation__list a {
    color: #ffffff; }

.PushRight {
  transform-origin: 100% 50%;
  animation: rotatePushRight .8s both ease;
  animation-delay: .180s; }

.PullRight {
  transform-origin: 100% 50%;
  animation: rotatePullRight .5s both ease;
  animation-delay: .180s; }

@keyframes rotatePullRight {
  from {
    opacity: 0;
    -webkit-transform: rotateY(-90deg);
    transform: rotateY(-90deg); } }

@keyframes rotatePushRight {
  from { }
  to {
    opacity: 0;
    -webkit-transform: rotateY(-90deg);
    transform: rotateY(-90deg); } }

